import { ViewPresentSubscriptionComponent } from "./view-present-subscription/view-present-subscription.component";
import { ViewSingleSubscriptionComponent } from "./view-single-subscription/view-single-subscription.component";
import { SubscriptionComponent } from "./subscription-page/subscription.component";
import { NgModule } from "@angular/core";
import { ViewAllPackagesComponent } from "./view-all-packages/view-all-packages.component";
import { Routes, RouterModule } from "@angular/router";

const routes: Routes = [
  {
    path: "",
    component: SubscriptionComponent,
    children: [
      { path: "", pathMatch: "full", redirectTo: "packages" },

      { path: "packages", component: ViewAllPackagesComponent },
      {
        path: "subscription-details",
        component: ViewPresentSubscriptionComponent,
      },
      { path: "package/:type", component: ViewSingleSubscriptionComponent },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class SubscriptionRoutingModule {}
export const routedComponents = [
  ViewAllPackagesComponent,
  ViewPresentSubscriptionComponent,
  ViewSingleSubscriptionComponent,
  SubscriptionComponent,
];
