import { PlanSubscriptionService } from "../../../@core/plans-subscription.service";
import { MENU } from "../../page/pages.menu";
import { User } from "../../../@model/user";
import { Component, OnInit, AfterViewInit } from "@angular/core";
import { ToastrService } from "ngx-toastr";
import { AuthService, GeneralService } from "src/app/@core";
import { Subscription } from "rxjs";

@Component({
  selector: "app-subscription",
  templateUrl: "./subscription.component.html",
  styleUrls: [
    "../../page/page.component.scss",
    "./subscription.component.scss",
  ],
})
export class SubscriptionComponent implements OnInit {
  user: User;

  constructor(
    public authService: AuthService,
    private generalService: GeneralService
  ) {}

  ngOnInit(): void {
    this.getUserInfo();
  }

  getUserInfo() {
    this.user = this.authService.getUserLocally();
  }
}
